<template>
  <div class="">

<container>
    <row>
      <card class="card" reverse>
        <view-wrapper hover cascade>
          <div class="card-img">
            <img class="mw-100 card" src="../assets/img/bers.jpg" alt="Card image cap" >
          </div>
          <md-mask waves overlay="white-slight"></md-mask>
        </view-wrapper>
        <card-body class="text-center" cascade>
          <card-title><strong>BERSERKER MTL STARTER KIT</strong></card-title>
          <h5 class="indigo-text"><strong>starter kit</strong></h5>
          <card-text>
            Vandy Vape Berserker MTL Kit (1100 mAh) -
               Vandy Vape, 
               Alex from VapersMD 
             Berserker,   
                
               .   
                
               , 
              ,   
             .
          </card-text>
          <button class="btn btn-outline-success" v-on:click="view"></button>
            <a class="btn btn-outline-success" href="#"></a>
        </card-body>
      </card>
    </row>

  </container>
  <div v-if="isVideo" class="example-video" v-on:click="view">
    <div class="col-lg-8 video-frame">
      <iframe
        width="760"
        height="515"
        src="https://www.youtube.com/embed/YMm-2vZFDgY"
        frameborder="0"
        allow="autoplay; encrypted-media"
        allowfullscreen>
      </iframe>
    </div>
  </div>
  </div>
</template>
<script type="text/javascript">
import { Row, ViewWrapper, MdMask, Container, Card, CardImg, CardBody, CardTitle, CardText, Btn } from 'mdbvue';

export default {
name: 'Phobia',
components: {
  Card,
  CardImg,
  CardBody,
  CardTitle,
  CardText,
  Btn,
  ViewWrapper,
  MdMask,
  Row,
  Container
},
data () {
  return {
    isVideo: false
  }
},
methods: {
  view () {
    this.isVideo = !this.isVideo
  }
}
};
</script>
<style lang="css">
.example-video {
  position: fixed;
  background: rgba(0,0,0, .8);
  z-index: 770;
  top: 0;
  min-height: 100vh;
  min-width: 100%;
}
.video-frame {
  position:absolute;
  z-index: 777;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin: 0 auto;
}
</style>



